/* General Reset and Body Styling */
/* CSS Reset for body and header to ensure no extra margins or paddings */
h1, h2 {
  font-family: regular;
}

h3 {
  font-family: bold;
}
p {
  font-family: text;
  font-size: 1rem;
}
.link {
  color: #8c8c73;
}
a {
  text-decoration: none;
}
body,
html, 
#root,
.App {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
  background-color: #ddddd5;
}
/* Header Styling */
.header, .headerimg {
  width: 100%;
  position: relative;
  /* Ensure padding/margin don't affect width */
}
/* Header Image Styling */
.headerimg {
  height: 50vh; /* Adjust height as needed */
  object-fit: cover;
  display: block; /* Remove inline-block whitespace issues */
}
/* Navigation Bar Styling */
.nav-bar, .knoopnavy {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 6%;
  z-index: 2;
  top: 70%;
  width: 100%;
  box-sizing: border-box; /* Ensure padding/margin don't affect width */
}
.nav-bar{
  position: absolute;
}
.knoopnavy {
  position: relative;
}
.logo img, .logosgv img {
  height: auto; /* Maintain aspect ratio */
  display: block; /* Ensure the image doesnâ€™t have inline spacing issues */
  filter: url(#drop-shadow);
}
.logo img{
  max-width: 35vh;
}
.logosgv img {
  max-width: 17vh; /* Adjust the max-width as needed */
}
/* Logo Styling */
.logo, .logosgv {
  position: absolute;
  top: 40%;
  transform: translate(-50%, -50%);
}
.logosgv {
  left: 90%;
}
.logo{
  left: 50%;
}
.logosgv img {
  max-width: 17vh; /* Adjust the max-width as needed */

}
/* Logo Styling */

/* Navigation List Styling */
.nav, .knoopnav {
  display: flex;
  list-style: none;
  padding: 15px;
  margin: 0;
  background-color: #8c8c73;
  border-radius: 15px;
  align-content: center;
}
.nav li, .knoopnav li {
  display: flex;
  align-items: center;

  padding: 0 20px;
}
.nav a, .knoopnav a {
  text-decoration: none;
  color: #262626;
  font-size: 1.5em;
  font-family: text;
  transition: color 0.3s ease;
}
.nav a:hover, .knoopnav a:hover {
  color: #ddddd5;
}
/* Responsive Styles */
.MasterGrid, .verhuurGrid, .widgetGrid, .eventGrid {
  width: 100%;
  box-sizing: border-box;
  padding-bottom: var(--gap);
  padding-top: var(--gap);
  padding-left: var(--gap);
  padding-right: var(--gap);
  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  grid-auto-rows: var(--row-height);
  gap: var(--gap);
}
.MasterGrid{
  --gap: 6vw;
  --num-cols: 4;
  --row-height: 22vw;
}
.verhuurGrid {
  --gap: 2vw;
  --num-cols: 4;
  --row-height: 15.5vw;
 
}
.widgetGrid {
  --gap: 3vw;
  --num-cols: 3;
  --row-height: 20.5vw;
  background-color: #8c8c73;
  
}
.eventGrid {
  --gap: 3vw;
  --num-cols: 3;
  --row-height: 10.5vw;
}
.MasterGrid > div {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: relative;
}
.overflow {
  overflow: hidden;
}

.verhuurGrid > div, .cards>div, .cardst>div {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: relative;
  overflow: hidden;
}

.cards>div, .cardst>div {
  background-color: aliceblue;
  border-radius: 10px;
}
.cards > div img {
  height: 40%;
  width: 100%;
  object-fit: cover;
  position: relative;
  overflow: hidden;
  background-color: aliceblue;
}
.cardst > div img {
  height: 60%;
  width: 100%;
  object-fit: contain;
  position: relative;
  overflow: hidden;
  background-color: aliceblue;
}
.cardst > div h2 {
  display: flex;
  justify-content: center;
  font-size: 2vw;
}
.grid-col-4 {
  grid-column: span 4;
}
.grid-row-4 {
  grid-row: span 4;
}
.grid-col-2 {
  grid-column: span 2;
}
.grid-row-2 {
  grid-row: span 2;
}
.grid-col-3 {
  grid-column: span 3;
}
.grid-row-3 {
  grid-row: span 3;
}
.orange {
  color: orange;
  font-family: bold;
}
.input, .menu-icon {
  display: none;
}
.imgTrooper {
  max-height: 40px;
  width: auto;
  object-fit: contain;
  position: relative;
  display: flex;
  align-items: center;
}
.textelement {
  margin: 6vw;
}
.knoop {
  margin-top: 0vw;
}
