@media screen and (max-aspect-ratio: 13/9), (max-width: 800px) {
  .grid-col-3 {
    grid-column: span 4;
  }
  .nav a:hover {
    color: #8c8c73;
  }
  .nav-bar {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 0;
  }
  .menu-icon {
    position: fixed;
    top: 20px;
    left: 20px;
    display: flex;
    cursor: pointer;
    color: #8c8c73;
    width: 30px;
    height: 30px;
    font-size: 7vh;
    z-index: 1000; /* Ensure the menu icon is on top */
  }
  .nav {
    display: none; /* Hide the menu by default */
    position: fixed;
    top: 0;
    left: 0;
    width: 250px; /* Adjust width as needed */
    height: 100%;
    background-color: #ddddd5;
    padding-top: 180px; /* Space for the menu icon */
    padding-bottom: 30px;
    border-radius: 0px;
    transform: translateX(-100%); /* Hide off-screen by default */
  }
  #menu-toggle:checked + label + .nav {
    display: flex;
    flex-direction: column;
    transform: translateX(0); /* Slide in from the left */
  }
  #menu-toggle {
    display: none; /* Hide the checkbox */
  }
  .nav a {
    display: block;
    padding: 10px 20px;
  }
  .g1-card {
    grid-column: span 2;
    grid-row: span 2;
  }
  .logo {
    top: 50%;
  }
  .logosgv {
    top: 50%;
  }
  .knoopnav a {
    font-size: 1em;
  }
  .knoopnav li {
    display: flex;
    align-items: center;
    padding: 0 10px;
  }
}
@media screen and (max-width: 600px) {
  .grid-col-3 {
    grid-row: span 6;
  }
  .grid-row-3 {
    grid-row: span 4;
  }
  .overflow {
    grid-row: span 5;
    grid-column: span 4;
  }
  .widgetGrid {
    --num-cols: 4;
    --row-height: 23.5vw;
  }
  .g1-card {
    grid-column: span 4;
    grid-row: span 4;
  }
  .widgetGrid > div {
    grid-row: span 2;
    grid-column: span 2;
  }
  .container1 {
    flex-direction: column;
  }
}